import { RendererCanvas, getImgFromURL } from '../../../lib';

export enum ColorCorrection {
    AutoContrast = 'autoContrast',
    None = 'None'
};
export class Solution {
    public divHost: HTMLElement;
    public renderer: Renderer;
    constructor(divHost: any) {
        this.divHost = divHost;

        // Center align
        this.divHost.style.display = 'flex';
        this.divHost.style.alignItems = 'center';
        const div = document.createElement('div');
        div.style.marginLeft = 'auto';
        div.style.marginRight = 'auto';
        div.style.backgroundColor = '#555';
        div.style.margin = '50px';
        div.style.background = 'rgba(0,0,0,0)';
        this.divHost.appendChild(div);

        this.renderer = new Renderer(div);
    }
    public destroy() {
        this.renderer.destroy();
        while (this.divHost.lastElementChild) {
            this.divHost.removeChild(this.divHost.lastElementChild);
        }
    }
};
export class Renderer extends RendererCanvas {
    img: HTMLImageElement | undefined = undefined
    rotation = 0.0;
    m: number[] = [0, 0];

    switch = true;
    isDithering = false;
    typeCorrection: ColorCorrection = ColorCorrection.AutoContrast;
    
    params = {
        "blur" : 0,
        "sepia": 0,
        "contrast": 1,
        "brightness": 1,
        "saturate": 1,
        "hueRotate": 0,
    };
    constructor(div: any) {
        super(div);
        this.ctx = this.canvas.getContext('2d', { willReadFrequently: true }) as CanvasRenderingContext2D;

        this.canvas.style.position = 'relative';
        this.canvas.style.width = '100%';
        this.canvas.style.height = '100%';

        getImgFromURL('https://upload.wikimedia.org/wikipedia/commons/f/ff/Mozilla_Firefox_logo_2013.png').then((img: HTMLImageElement) => {
            this.img = img;
            this.img.onload = (ev) => {
                this.update(img);
            }
        });


        const slType = getSlider('saturate', 0, 200)
        slType.slider.defaultValue = '100';
        this.divHost.appendChild(slType.div);
        slType.slider.oninput = () => {
            const value = parseInt(slType.slider.value) * 0.01;
            console.log(value);
            this.params.saturate = value
            this.render(this.ctx);
        };
        const slType2 = getSlider('hueRotate', -360, 360)
        slType2.slider.defaultValue = '0';
        this.divHost.appendChild(slType2.div);
        slType2.slider.oninput = () => {
            const value = parseInt(slType2.slider.value) * 1;
            console.log(value);
            this.params.hueRotate = value
            this.render(this.ctx);
        };
        const slType3 = getSlider('contrast', 0, 20)
        slType3.slider.defaultValue = '10';
        this.divHost.appendChild(slType3.div);
        slType3.slider.oninput = () => {
            const value = parseInt(slType3.slider.value) * .1;
            console.log(value);
            this.params.contrast = value
            this.render(this.ctx);
        };
        const slType4 = getSlider('brightness', 0, 20)
        slType4.slider.defaultValue = '10';
        this.divHost.appendChild(slType4.div);
        slType4.slider.oninput = () => {
            const value = parseInt(slType4.slider.value) * .1;
            console.log(value);
            this.params.brightness = value
            this.render(this.ctx);
        };
        const slType5 = getSlider('sepia', 0, 100)
        slType5.slider.defaultValue = '0';
        this.divHost.appendChild(slType5.div);
        slType5.slider.oninput = () => {
            const value = parseInt(slType5.slider.value) * .01;
            console.log(value);
            this.params.sepia = value
            this.render(this.ctx);
        };
        this.start();
    }
    public update(img: HTMLImageElement) {
        this.img = img;
        this.canvas.width = this.img.width;
        this.canvas.height = this.img.height;
        this.render(this.ctx);
    }
    public render(ctx: CanvasRenderingContext2D): void {
        if (this.img === undefined) { return; }

        const params = this.params;

        // for(let i = 0 ; i < filters.length; ++i) {
        //     params[filters[i][0]] = filters[i][1];
        // }
        // console.log(params);

        const vertexShaderSource = `
        attribute vec2 a_position;
        attribute vec2 a_texCoord;
        varying vec2 v_texCoord;

        void main() {
            gl_Position = vec4(a_position, 0, 1);
            v_texCoord = a_texCoord;
        }
        `;

        const fragmentShaderSource = `
        precision mediump float;
        
        uniform vec2 u_resolution;
        uniform float u_blurRadius;
        uniform float u_sepia;
        uniform float u_contrast;
        uniform float u_brightness;
        uniform float u_saturation;
        uniform float u_hueRotation;
        uniform sampler2D u_texture;
        varying vec2 v_texCoord;

        vec4 blur5(sampler2D image, vec2 uv, vec2 resolution, vec2 direction) {
            vec4 color = vec4(0.0);
            vec2 off1 = vec2(1.3333333333333333) * direction;
            color += texture2D(image, uv) * 0.29411764705882354;
            color += texture2D(image, uv + (off1 / resolution)) * 0.35294117647058826;
            color += texture2D(image, uv - (off1 / resolution)) * 0.35294117647058826;
            return color; 
          }

        void main() {
            vec2 flippedTexCoord = vec2(v_texCoord.x, 1.0 - v_texCoord.y);
            vec4 color = texture2D(u_texture, flippedTexCoord);


            // ..................
            float gray = dot(color.rgb, vec3(0.299, 0.587, 0.114));
            vec3 sepia = vec3(1.0, 0.95, 0.82);
            color = mix(color, vec4(gray * sepia, color.a), u_sepia);


            // ..................
            vec3 colorRgb = color.rgb * u_brightness;
            colorRgb = (colorRgb - vec3(0.5)) * max(u_contrast, 0.0) + vec3(0.5);
            color = vec4(colorRgb, color.a);


            // ..................
            color = vec4(mix(vec3(gray), color.rgb, u_saturation), color.a);

            
            // ..................
            float angle = u_hueRotation * 3.14159265358979323846264 / 180.0;
            float s = sin(angle);
            float c = cos(angle);
            mat3 hueRotationMatrix = mat3(
            0.213 + 0.787 * c - 0.213 * s, 0.715 - 0.715 * c - 0.715 * s, 0.072 - 0.072 * c + 0.928 * s,
            0.213 - 0.213 * c + 0.143 * s, 0.715 + 0.285 * c + 0.140 * s, 0.072 - 0.072 * c - 0.283 * s,
            0.213 - 0.213 * c - 0.787 * s, 0.715 - 0.715 * c + 0.715 * s, 0.072 + 0.928 * c + 0.072 * s
            );
            colorRgb = color.rgb * hueRotationMatrix;
            color = vec4(colorRgb, color.a);


            // ..................
            gl_FragColor = color;
        }
        `;

        const image = this.img;
        const canvasGL: HTMLCanvasElement = document.createElement('canvas');
        canvasGL.width = image.width;
        canvasGL.height = image.height;

        const gl: WebGLRenderingContext = canvasGL.getContext('webgl2');
        const texture: WebGLTexture = gl.createTexture();
        gl.bindTexture(gl.TEXTURE_2D, texture);
        gl.texImage2D(gl.TEXTURE_2D, 0, gl.RGBA, gl.RGBA, gl.UNSIGNED_BYTE, image);
        gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_S, gl.CLAMP_TO_EDGE);
        gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_T, gl.CLAMP_TO_EDGE);
        gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MIN_FILTER, gl.LINEAR);
        gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MAG_FILTER, gl.LINEAR);

        const vertexShader: WebGLShader = gl.createShader(gl.VERTEX_SHADER);
        gl.shaderSource(vertexShader, vertexShaderSource);
        gl.compileShader(vertexShader);

        const fragmentShader: WebGLShader = gl.createShader(gl.FRAGMENT_SHADER);
        gl.shaderSource(fragmentShader, fragmentShaderSource);
        gl.compileShader(fragmentShader);

        const program: WebGLProgram = gl.createProgram();
        gl.attachShader(program, vertexShader);
        gl.attachShader(program, fragmentShader);
        gl.linkProgram(program);
        gl.useProgram(program);

        const positionAttributeLocation: number = gl.getAttribLocation(program, 'a_position');
        const texCoordAttributeLocation: number = gl.getAttribLocation(program, 'a_texCoord');
        const textureUniformLocation: WebGLUniformLocation = gl.getUniformLocation(program, 'u_texture');
        gl.uniform1i(textureUniformLocation, 0);

        const positionBuffer: WebGLBuffer = gl.createBuffer();
        gl.bindBuffer(gl.ARRAY_BUFFER, positionBuffer);
        gl.bufferData(gl.ARRAY_BUFFER, new Float32Array([-1, -1, 1, -1, -1, 1, 1, 1]), gl.STATIC_DRAW);
        gl.enableVertexAttribArray(positionAttributeLocation);
        gl.vertexAttribPointer(positionAttributeLocation, 2, gl.FLOAT, false, 0, 0);

        const texCoordBuffer: WebGLBuffer = gl.createBuffer();
        gl.bindBuffer(gl.ARRAY_BUFFER, texCoordBuffer);
        gl.bufferData(gl.ARRAY_BUFFER, new Float32Array([0, 0, 1, 0, 0, 1, 1, 1]), gl.STATIC_DRAW);
        gl.enableVertexAttribArray(texCoordAttributeLocation);
        gl.vertexAttribPointer(texCoordAttributeLocation, 2, gl.FLOAT, false, 0, 0);

        gl.activeTexture(gl.TEXTURE0);
        gl.bindTexture(gl.TEXTURE_2D, texture);

        const sepiaUniformLocation = gl.getUniformLocation(program, 'u_sepia');
        gl.uniform1f(sepiaUniformLocation, params.sepia);
        const saturationUniformLocation = gl.getUniformLocation(program, 'u_saturation');
        gl.uniform1f(saturationUniformLocation, params.saturate);
        const brightnessUniformLocation = gl.getUniformLocation(program, 'u_brightness');
        gl.uniform1f(brightnessUniformLocation, params.brightness);
        const contrastUniformLocation = gl.getUniformLocation(program, 'u_contrast');
        gl.uniform1f(contrastUniformLocation, params.contrast);
        const hueRotationUniformLocation = gl.getUniformLocation(program, 'u_hueRotation');
        gl.uniform1f(hueRotationUniformLocation, params.hueRotate);

        const blurUniformLocation = gl.getUniformLocation(program, 'u_blurRadius');
        gl.uniform1f(blurUniformLocation, 1.8);

        const resolutionUniform = gl.getUniformLocation(program, 'u_resolution');
        gl.uniform2f(resolutionUniform, gl.canvas.width, gl.canvas.height);
        
        gl.drawArrays(gl.TRIANGLE_STRIP, 0, 4);

        ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
        ctx.drawImage(canvasGL, 0, 0);

        gl.deleteProgram(program);
        gl.deleteTexture(texture);
        gl.deleteShader(vertexShader);
        gl.deleteShader(fragmentShader);
        gl.deleteBuffer(positionBuffer);
        gl.deleteBuffer(texCoordBuffer);

        this.isStatic = true;
    }
}

const getSlider = (title: string, min: number, max: number) => {
    // <input type="range" min="1" max="100" value="50" class="slider" id="myRange">
    const div = document.createElement('div');
    div.style.display = 'flex';

    const slider = document.createElement('input');
    slider.type = 'range';
    slider.min = `${min}`;
    slider.max = `${max}`;

    const text = document.createElement('div');
    text.textContent = title;
    text.style.color = '#aaa';
    div.appendChild(slider);
    div.appendChild(text);
    return {
        div, slider, text
    }
};